:root {
  --main-bg-color: beige;
  --primary-text-color: rgb(159, 0, 138);
}

@mixin other-pages {
  text-align: center;
  color: var(--primary-text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

html {
  font-size: 1vw;
  height: 100%;
  margin: 0;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--main-bg-color);
	height: 100%;
}

.main {
  width: 100%;
	flex: 1;
}

.certificate {
  width: 100%;
  height: 100%;
  .certificates-list {
    display: flex;
    margin-top: 1%;
    width: 100%;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    .certificate-item {
      display: flex;
      font-size: 1.3em;
      width: 50%;
      height: 70%;
      padding: 20px;
      background-color: white;
      border-radius: 5px;
      margin-bottom: 10px;
      box-shadow: 0px 4px 8px 0px rgba(148, 148, 148, 0.2);
      &:hover {
        box-shadow: 0px 4px 8px 0px rgba(202, 148, 255, 0.61);
        background-color: #fffcfe;
      }
      .image-certificate {
        width: 200px;
        height: 200px;
        background-color: gray;
        color: white;
        text-align: center;
      }
      .information-certificate {
        padding: 5px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .certificate-summ {
          width: 100%;
          display: flex;
          justify-content: space-between;
          .summ {
            .price {
              text-decoration: line-through;
            }
          }
          .certificate-buy-button {
            cursor: pointer;
            outline: none;
            border: 1px solid gray;
            border-radius: 5px;
            background-color: transparent;
            transition: 0.2s ease-in-out;
            width: 15%;
            &:active {
              transform: scale(0.9);
            }
          }
        }
      }
    }
  }
}

.header-sitelogo {
  .company-name {
    display: block;
    text-decoration: none;
    color: var(--primary-text-color);
    font-size: 3em;
  }
}

.payment {
  @include other-pages;
  font-size: 2em;
}

.not-found {
  @include other-pages;
  font-size: 4em;
}
